<template>
    <div class="mls-common-table">
        <lb-table
            ref="table"
            v-bind="{ ...getPaginationConfig, ...getRestConfig, pagination }"
            :column="getColumn"
            highlight-current-row
            v-on="$listeners"
            @selection-change="onTableSelectionChange"
            @current-change="onTableCurrentChange"
        />
        <div :class="getSelectInfoBoxStyle">
            <div class="page-text" v-if="hasSelection">
                <!--  -->
                <span></span>
                <el-popover placement="top" width="400" trigger="manual" ref="selectPopover" v-model="visible">
                    <div class="pop-head">
                        <span class="title"></span>
                        <i class="el-icon-close close" @click="visible = !visible"></i>
                    </div>
                    <lb-table :column="getSelectedTableColumn" :data="selectedRows" border />
                </el-popover>
                <el-link v-popover:selectPopover type="primary" class="selected-num" @click="visible = !visible">{{
                    checkedNum
                }}</el-link>
                <span></span>
            </div>
            <slot name="selectButtonSlot" class="function-button-group">
                <!--  -->
            </slot>
        </div>
    </div>
</template>
<script>
import LbTable from './lb-table'
import { pick, omit } from 'lodash'

// 
const defaultPaginationConfig = {
    pageSizes: [10, 20, 30, 50, 100],
    layout: 'total, sizes, prev, pager, next',
    pageSize: 10,
    pagerCount: 5,
    background: true
}
/**
 * lb-table
 * el-tableel-table
 * el-table
 */
export default {
    name: 'MlsCommonTable',
    components: {
        LbTable
    },
    props: {
        // 
        pagination: {
            type: Boolean,
            default: false
        },
        // table
        selectedColumn: {
            type: Array
        },
        // ratio
        hasRatioSelect: {
            type: Boolean,
            default: false
        },
        // 
        hasSelectedSumary: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            visible: false,
            selectedRows: [],
            selectedRowKey: ''
        }
    },
    computed: {
        getPaginationConfig () {
            if (this.pagination) {
                return {
                    ...defaultPaginationConfig,
                    ...pick(this.$attrs, Object.keys(defaultPaginationConfig))
                }
            }
            return {
                ...pick(this.$attrs, Object.keys(defaultPaginationConfig))
            }
        },
        getRestConfig () {
            return {
                paginationTop: '10px',
                ...omit(this.$attrs, Object.keys(defaultPaginationConfig))
            }
        },
        hasSelection () {
            if (this.hasSelectedSumary) {
                if (this.$attrs.column) {
                    return this.$attrs.column.find(item => item.type === 'selection') || this.hasRatioSelect
                }
                return this.hasRatioSelect
            }
            return false
        },
        getSelectedTableColumn () {
            let defaultColumn = this.$attrs.column.filter(item => item.type !== 'selection' && item.type !== 'index').slice(0, 3)
            if (this.selectedColumn) {
                defaultColumn = [...this.selectedColumn]
            }
            defaultColumn.push({
                label: '',
                fixed: 'right',
                render: (h, scope) => {
                    return (
                        <div>
                            <el-button type='text' onClick={() => this.onUnselectItem(scope.row)}>
                                
                            </el-button>
                        </div>
                    )
                }
            })
            return defaultColumn
        },
        checkedNum () {
            if (this.selectedRows) {
                return this.selectedRows.length
            } else if (this.selectedRowKey) {
                return 1
            }
            return 0
        },
        getColumn () {
            let column = []
            if (this.$attrs.column) {
                column = [...this.$attrs.column]
            }
            if (this.hasRatioSelect && this.$attrs['row-key']) {
                const ratioColumn = {
                    label: '',
                    width: '50',
                    fixed: true,
                    render: (h, scope) => {
                        return (
                            <el-radio
                                label={scope.row[this.$attrs['row-key']]}
                                v-model={this.selectedRowKey}
                                onChange={() => this.onTableRatioChange(scope.row)}>
                                &nbsp;
                            </el-radio>
                        )
                    }
                }
                column.unshift(ratioColumn)
            }
            return column
        },
        // box
        getSelectInfoBoxStyle () {
            let styleClass = 'select-info-box'
            if (!this.pagination) {
                styleClass += ' relative-box'
            }
            return styleClass
        }
    },
    methods: {
        onUnselectItem (row) {
            if (this.$attrs.column.find(item => item.type === 'selection')) {
                this.$refs['table'].toggleRowSelection(row, false) // 
            } else if (this.hasRatioSelect) {
                this.selectedRows = []
                this.selectedRowKey = ''
            }
        },
        onTableSelectionChange (selection) {
            this.selectedRows = selection // row
        },
        onTableRatioChange (row) {
            this.selectedRows = [{...row}]
            this.selectedRowKey = row[this.$attrs['row-key']]
        },
        onTableCurrentChange (row) {
            // 
            if (this.hasRatioSelect) {
                // 
                if (typeof row === 'object') {
                    // row object row number
                    this.selectedRows =[{...row}]
                    this.selectedRowKey = row[this.$attrs['row-key']]
                }
            }
        }
    }
}
</script>
<style lang="less">
.mls-common-table {
    position: relative;
    .select-info-box {
        display: flex;
        position: absolute;
        left: 0;
        bottom: 5px;
        .page-text {
            display: flex;
            font-size: 13px;
            line-height: 28px;
            text-align: center;
            margin-right: 15px;
            .selected-num {
                padding: 0 5px;
                line-height: 1.2;
                font-size: 14px;
                color: #00bcbf;
            }
        }
        .function-button-group {
        }
    }
    .relative-box {
        position: relative;
        margin-top: 10px;
    }
}
.pop-head {
    display: flex;
    justify-content: space-between;
    padding-bottom: 10px;
    .title {
        font-size: 14px;
        line-height: 20px;
        color: #303133;
    }
    .close {
        font-size: 18px;
        color: #909399;
        cursor: pointer;
    }
}
</style>
